.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Header */
/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.header {
  background: linear-gradient(to right, #1d1d1d, #2a2a72);
  color: white;
  text-align: center;
  padding: 20px 0;
}

.header-content {
  max-width: 900px;
  margin: 0 auto;
}

/* Logo Section */
.logo img {
  width: 150px;
  margin-bottom: 20px;
}

/* Tagline */
.tagline h1 {
  font-size: 26px;
  margin: 10px 0;
}

.tagline .highlight {
  color: #ff5733;
  font-weight: bold;
}

.tagline p {
  font-size: 16px;
  margin-bottom: 30px;
}

/* Phone Mockup */
.phone-mockup {
  position: relative;
  margin-bottom: 30px;
}

.phone-mockup img {
  width: 100%;
  max-width: 300px;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}

.quiz-box {
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 15px 20px;
  border-radius: 15px;
  width: 80%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.quiz-box p {
  margin: 0 0 10px;
  font-size: 14px;
  font-weight: bold;
}

.options {
  display: flex;
  justify-content: space-between;
}

.option {
  background-color: #ff5733;
  border: none;
  border-radius: 10px;
  color: white;
  padding: 10px 15px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.option:hover {
  background-color: #ff7043;
}

/* CTA Section */
.cta-section {
  margin-top: 20px;
}

.cta-section p {
  font-size: 14px;
  margin: 0 0 10px;
}

.download-btn {
  background-color: #ff5733;
  color: white;
  border: none;
  border-radius: 20px;
  padding: 10px 25px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.download-btn:hover {
  background-color: #ff7043;
}

/* feature */
.features-container {
  font-family: Arial, sans-serif;
  background-color: #0d1b2a;
  color: white;
  text-align: center;
  padding: 30px 20px;
}

.features-header {
  margin-bottom: 40px;
}

.features-header h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.features-header .highlight {
  color: #ff5733;
}

.features-stats {
  font-size: 16px;
  margin-bottom: 20px;
}

.payment-icons img {
  height: 30px;
  margin: 0 10px;
}

.features-section {
  background-color: #192841;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 40px;
}

.features-section h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.features-subtitle {
  font-size: 14px;
  margin-bottom: 20px;
}

.features-grid {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.feature-item {
  text-align: center;
  color: #ff5733;
}

.feature-icon {
  font-size: 40px;
  margin-bottom: 10px;
}

.languages-section h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.languages-section p {
  font-size: 14px;
  margin-bottom: 20px;
}

.languages-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}

.language-button {
  background-color: #ff5733;
  color: white;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.language-button:hover {
  background-color: #ff7043;
}

/* General styles for the Testimonials section */
.testimonials {
  background-color: #0d1326;
  color: #fff;
  padding: 60px 20px;
  text-align: center;
}

.testimonials h2 {
  font-size: 2.2rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #ffffff;
}

.testimonials h2 span {
  font-style: italic;
  color: #ffcc00;
}

.testimonials p {
  font-size: 1.1rem;
  margin-bottom: 30px;
  color: #cccccc;
}

.reviews {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  justify-content: center;
  align-items: stretch;
}

.review {
  background-color: #18204f;
  border: 1px solid #444b71;
  border-radius: 10px;
  padding: 20px;
  text-align: left;
  position: relative;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  color: #fff;
  overflow: hidden;
}

.review::before {
  content: "â€œ";
  font-size: 80px;
  color: rgba(255, 255, 255, 0.1);
  position: absolute;
  top: -20px;
  left: 20px;
  z-index: 0;
}

.review p {
  font-size: 1rem;
  font-style: italic;
  margin: 15px 0;
  color: #ddd;
}

.review h4 {
  font-size: 1rem;
  font-weight: bold;
  margin-top: 15px;
  color: #ffcc00;
}

.review p:last-of-type {
  font-size: 0.9rem;
  color: #aaa;
}

/* General Footer Styles */
.footer {
  background-color: #0d1326;
  color: #ffffff;
  text-align: center;
  padding: 40px 20px;
}

.footer-top {
  margin-bottom: 30px;
}

.footer-logo {
  height: 50px;
  margin-bottom: 10px;
}

.tagline {
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffcc00;
}

.footer-links {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.footer-column h4 {
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
}

.footer-column li {
  font-size: 0.9rem;
  color: #cccccc;
  margin-bottom: 5px;
}

.footer-social {
  margin-bottom: 30px;
}

.social-icon {
  display: inline-block;
  color: #ffffff;
  font-size: 1.5rem;
  margin: 0 10px;
  transition: color 0.3s;
}

.social-icon:hover {
  color: #ffcc00;
}

.footer-bottom {
  font-size: 0.9rem;
  color: #cccccc;
  margin-bottom: 30px;
}

.footer-bottom p {
  margin: 5px 0;
}

.disclaimer {
  margin-top: 10px;
  font-size: 0.8rem;
  color: #aaaaaa;
}

.footer-bonus {
  background-color: #1c2541;
  padding: 20px;
  border-radius: 10px;
  margin: 0 auto;
  max-width: 400px;
}

.footer-bonus p {
  font-size: 1rem;
  color: #ffcc00;
  margin-bottom: 10px;
}

.footer-bonus span {
  font-weight: bold;
}

.download-btn {
  background-color: #ff414d;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.download-btn:hover {
  background-color: #d8343e;
}
